.contacP{
    height: 100vh;
    display: flex;
    justify-content: center;
}
.contacto{
    height: 90vh;
    width: 95%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.separar{
    padding-left: 15px;
    padding-top: 15px;

}
.contacto1{
    height: 90vh;
    width: 60%;
    background-color: rgb(13, 13, 14);
    color: white;
    margin-top: 20px;

}
.formu{
    text-align: initial;
    width: 85%;
    margin-left: 10px;
    align-self: center;
}
.Titulo{
    text-align: center;
}
.carruselc{
    margin-top: 20px;
    margin-right: 10px;
}

@media (max-width: 800px) {
    .contacto{
        flex-direction: column;
    }
    .contacto1{
        height: 115vh;
        margin: 0px;
        width: 100%;
        
        
    }
    .contacP{
        height: 115vh;
    }
}