.biografia{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.historia{
    width: 80%;
    height: 70%;
    background-color: antiquewhite;
    display: flex;
}
.tama{
    height: 100%;
}
.der{
    width: 60%;
    height: 100%;
    background-color: rgb(43, 19, 19);
    text-justify: auto;
    color: white;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
}
.boton{
    width: 20vh;
    align-self: center;
}
.izq{
    width: 40%;
    height: 100%;
    background-color: rgb(243, 145, 25);
}
@media (max-width: 800px) {
    .biografia{

        height: 130vh;
    }
    .izq{
        width: 100%;
        height: 40%;
    }
    .der{
        width: 100%;
        height: 80vh;

    }
    .historia{
        flex-direction: column;
        
    }
}